<style>
    .particle-canvas {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
</style>

<template>
	<vue-particles 
        ref="particleCanvas"
        :particleOpacity="0.4"
        linesColor="#dedede"
        :particlesNumber="20"
        :particleSize="3"
        :linesDistance="220"
        :moveSpeed="5"
        class="particle-canvas"
    ></vue-particles>
</template>

<script>
	export default {
		name: 'ParticleCanvas',
		methods: {
			setCanvasSize(){
				this.$refs.particleCanvas.width = window.innerWidth;
				this.$refs.particleCanvas.height = window.innerHeight;
			}
		},
		mounted(){
			window.addEventListener('resize', this.setCanvasSize);
			this.setCanvasSize();
		},
		beforeDestroy(){
			console.log("I'm destroyed");
			window.removeEventListener('resize', this.setCanvasSize);
		}
	}
</script>


